import React from 'react'
import {
  Row,
  Col,
  TabContent,
  TabPane,
} from 'reactstrap'
import AddLiabilty from './AddLiabilty'
import '../../assets/scss/plugins/extensions/dropzone.scss'
class Liabilty extends React.Component {
  state = {
    active: '1'
  }

  toggle = (tab) => {
    if (this.state.active !== tab) {
      this.setState({ active: tab })
    }
  }

  render() {
    return (
      <React.Fragment>
        <h2 className="warning spacing">Liabilties</h2>
        <Row>
          <Col sm="12">
            <TabContent activeTab={this.state.active}>
              <TabPane tabId="1">
                <AddLiabilty />
              </TabPane>
            </TabContent>
          </Col>
        </Row>
      </React.Fragment>
    )
  }
}
export default Liabilty
